<template>
  <div>
    <div style="margin-right: 10px; margin-left: 10px;">

      <div class="boderDes">
        <h4 style="margin-left: 20px;">ATS映射设置</h4>
        <div style="margin-right: 20px; margin-left: 20px;">
          <el-form
            :inline="true"
            label-position="left"
          >

            <el-form-item label="线路选择">
              <el-select
                v-model="selectLineId"
                placeholder="请选择"
              >
                <el-option
                  v-for="item in lineList"
                  :key="item.id"
                  :label="item.id + ' ' +item.name"
                  :value="item.id"
                >
                </el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="设备类型">
              <el-select
                v-model="selectDevType"
                placeholder="请选择"
              >
                <el-option
                  v-for="item in devTypeList"
                  :key="item.id"
                  :label="item.id + ' ' +item.name"
                  :value="item.id"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item style="padding-left:20px">
              <el-button
                type="primary"
                icon="el-icon-plus"
                :disabled="btnDisable.ini"
                @click="initClick"
              >初始化</el-button>

              <el-button
                type="danger"
                icon="el-icon-delete"
                :disabled="btnDisable.del"
                @click="delClick"
              >删除</el-button>

              <el-button
                type="success"
                :disabled="btnDisable.save"
                @click="saveClick"
              >保存</el-button>
            </el-form-item>
          </el-form>

        </div>
      </div>
      <edit-table
        :table_ui_data="uiList"
        :table_data="tableData"
        :table_disable_edit="false"
        ref="edtable"
        :unique_id="['stationName','devName']"
        @rowClick='rowClick'
        @dataChanged="tableDataChanged"
      ></edit-table>
    </div>
    <tipDialog
      @okClick='dialogClick'
      :dialog='dialogOb'
    ></tipDialog>
  </div>
</template>

<script>
import { Message } from "element-ui";

import tipDialog from "../tipDialog.vue";

export default {
  components: {
    tipDialog,
  },
  data() {
    return {
      uiList: [
        {
          label: "车站名称",
          prop: "stationName",
          disabled: true,
        },
        { label: "设备名", prop: "devName", disabled: true },
        { label: "ATS编号1", prop: "ats1" },
        { label: "标记1", prop: "tag1" },
        { label: "ATS编号2", prop: "ats2" },
        { label: "标记2", prop: "tag2" },
        { label: "ATS编号3", prop: "ats3" },
        { label: "标记3", prop: "tag3" },
        { label: "ATS编号4", prop: "ats4" },
        { label: "标记4", prop: "tag4" },
      ],
      tableData: [],
      btnDisable: {
        ini: true,
        del: true,
        save: true,
      },
      lineList: [],
      selectLineId: "",
      devTypeList: [],
      selectDevType: "",
      dialogOb: { visible: false, title: "提示", tipMsg: "", trsfParm: {} },
      currentSelRow: undefined,
    };
  },
  mounted() {
    const that = this;

    this.$httpApi.lineInfo("0").then(function (response) {
      if (response.data.code == "0") {
        that.lineList = response.data.dataList;
        if (that.lineList && that.lineList.length > 0) {
          that.selectLineId = that.lineList[0].id;
          that.btnDisable.ini = false;
        } else {
          that.btnDisable.ini = true;
        }
      }
    });
    this.$httpApi
      .typdeList()
      .then(function (response) {
        // console.log(response.data);
        if (response.data.code == "0") {
          that.devTypeList = response.data.dataList;
        }
      })
      .catch(function () {});
  },
  watch: {
    lineAndDevTypeChanged(newV, oldV) {
      console.log("lineAndDevTypeChanged", newV, oldV);
      var that = this;
      this.$httpApi
        .ats_map_get(this.selectLineId, this.selectDevType)
        .then((resp) => {
          if (resp.data.code == "0") {
            that.tableData = resp.data.dataList;
            that.$refs.edtable.initCurrentData(that.tableData);
          }
        });
    },
  },
  computed: {
    lineAndDevTypeChanged() {
      const ll = this.selectLineId;
      const dev = this.selectDevType;
      return { ll, dev };
    },
  },
  methods: {
    initTableData() {
      var that = this;

      this.tableData = new Array();
      this.$httpApi
        .devlist_type_get(this.selectLineId, this.selectDevType)
        .then((resp) => {
          if (resp.data.code == "0") {
            let dataList = resp.data.dataList;
            dataList.forEach((element) => {
              var obj = {
                stationName: element.staName,
                devName: element.devName,
                devCode: element.devCode,
                stationCode: element.staCode,
                ats1: "",
                tag1: 0,
                ats2: "",
                tag2: 0,
                ats3: "",
                tag3: 0,
                ats4: "",
                tag4: 0,
              };
              that.tableData.push(obj);
            });
            //that.$refs.edtable.initCurrentData(that.tableData);
          } else {
            Message.error(resp.data.message);
          }
        });
    },
    initClick() {
      if (this.tableData && this.tableData.length > 0) {
        this.dialogOb.tipMsg = "初始化将清除列表中的数据,是否继续?";
        this.dialogOb.visible = true;
      } else {
        this.initTableData();
      }
    },
    dialogClick() {
      this.initTableData();
    },
    rowClick(val) {
      this.currentSelRow = val;
      this.btnDisable.del = val == undefined;
    },
    delClick() {
      var that = this;
      that.$refs.edtable.deleteAction(this.currentSelRow);
    },
    saveClick() {
      this.$refs.edtable.confirmOKAction((obj, typeNum) => {
        var postData = JSON.parse(JSON.stringify(obj));
        postData.lineId = this.selectLineId;
        if (typeNum == 1) {
          //增加
          console.log("add", obj);
          this.$httpApi.ats_map_addl(postData).then((resp) => {
            if (resp.data.code != "0") {
              Message.error(resp.data.message);
            }
          });
        } else if (typeNum == 2) {
          //变更
          console.log("upda");
          this.$httpApi.ats_map_updata(postData).then((resp) => {
            if (resp.data.code != "0") {
              Message.error(resp.data.message);
            }
          });
        } else if (typeNum == 3) {
          //删除
          console.log("del");
          this.$httpApi.ats_map_del(postData).then((resp) => {
            if (resp.data.code != "0") {
              Message.error(resp.data.message);
            }
          });
        }
      });
    },
    tableDataChanged(val) {
      this.btnDisable.save = val == 0;
    },
  },
};
</script>

<style>
.boderDes {
  border: 1px solid lightgray;

  margin-bottom: 20px;
}
</style>